import { Link } from "react-router-dom";
import axios from "axios";
import { useState } from "react";

import Loading from "../Loading";

import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import { Button } from "@mui/material";
import Swal from "sweetalert2";

const RoomsList = (prop) => {
  const name = sessionStorage.getItem("name");

  //  
  const [isLoading, setisLoading] = useState(false);

  //   
  const onClickDelete = (e, detail) => {
    e.preventDefault();
    const inputMsg = prompt(
      `    (${detail.title})  .`
    );
    if (inputMsg === detail.title) {
      console.log(".");
      setisLoading((prev) => !prev);
      deleteRoom(detail.num);
    } else {
      console.log("   .  .");
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "  .  .",
      });
    }

    console.log(e, detail);
  };

  //     
  const deleteRoom = async (num) => {
    axios.defaults.withCredentials = true;
    console.log(num);

    const res = await axios({
      method: "delete",
      url: `/room/${num}`,
    });
    setisLoading((prev) => !prev);

    if (res.status === 200) {
      Swal.fire({
        icon: "success",
        title: " .",
        showConfirmButton: false,
        timer: 1000,
      });
      setTimeout(() => {
        window.location.replace("/");
      }, 1000);
    }
  };

  return (
    <div>
      {isLoading ? <Loading /> : ""}
      <div
        style={{
          border: "2px solid skyblue",
          height: "500px",
          width: "1000px",
          maxWidth: "1000px",
          borderRadius: "45px 45px 45px 45px",
          margin: "15px auto",
        }}
      >
        <h1 style={{ textAlign: "center" }}></h1>
        <div style={{ width: "1000px" }}>
          {prop.rooms.map((detail) => {
            return (
              <div
                key={detail.code}
                style={{
                  float: "left",
                  margin: "15px 0px 0px 25px",
                  border: "3px solid black",
                  borderRadius: "20px 20px 20px 20px",
                  width: "450px",
                  boxSizing: "borderbox",
                }}
              >
                <Link
                  to={`/room/${detail.num}`}
                  style={{
                    color: "black",
                    textDecoration: "none",
                    margin: "0 0 0 10px",
                  }}
                >
                    : {detail.title}
                  <br />
                  <label style={{ margin: "0 0 0 10px" }}>
                      : {detail.introduce}
                  </label>
                </Link>
                <Button
                  onClick={(e) => onClickDelete(e, detail)}
                  endIcon={<DeleteForeverIcon />}
                  style={{ color: "red" }}
                >
                  
                </Button>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default RoomsList;
