import { useContext, useEffect, useState, useRef } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { Link, useParams, useLocation } from "react-router-dom";
import axios from "axios";
import uuid from "react-uuid";
import * as React from "react";

import RoomSetting from "./RoomSetting";
import ToDoDetail from "./ToDoDetail";

import ChatIcon from "@mui/icons-material/Chat";
import { Client } from "@stomp/stompjs";
import AddTaskIcon from "@mui/icons-material/AddTask";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
} from "@mui/material";

import PublishIcon from "@mui/icons-material/Publish";
import CancelIcon from "@mui/icons-material/Cancel";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DesktopDatePicker } from "@mui/x-date-pickers/DesktopDatePicker";
import { WifiTetheringErrorRounded } from "@mui/icons-material";

function RoomDetail() {
  const param = useParams().roomid; // url roomid

  const [userName, setName] = useState(sessionStorage.getItem("name"));

  const [inviteCode, setInviteCode] = useState("");

  //  todo    x
  const [loading, setLoading] = useState(false);

  //  toDos 
  const getToDos = async () => {
    const res = await axios({
      method: "get",
      url: `/room/${param}`,
    });
    if (res.status === 200) {
      console.log(res.data);
      setColumnsFromBackend(res.data);
    } else {
      console.log(res);
    }
  };

  //  inviteCode 
  const getInviteCode = async () => {
    const res = await axios({
      method: "get",
      url: `/room/${param}/code`,
    });
    if (res.status === 200) {
      setInviteCode(res.data);
    }
  };

  //  todos invite code 
  useEffect(() => {
    getToDos();
    getInviteCode();
  }, []);

  // Drag 
  const onDragStart = (result, columns, setColumns) => {
    // 06.07   isEditing  true  
    // columns[result.source.droppableId].items.forEach((element) => {
    //   if (element.id === result.draggableId) {
    //     element.isEdit = true;
    //   }
    // });
    // //  publish 
  };

  // Drag And Drop  
  const onDragEnd = (result, columns, setColumns) => {
    if (!result.destination) return;
    const { source, destination } = result;

    if (source.droppableId !== destination.droppableId) {
      // ,  column 
      const sourceColumn = columns[source.droppableId];
      const destColumn = columns[destination.droppableId];

      // ,  items  
      const sourceItems = [...sourceColumn.items];
      const destItems = [...destColumn.items];

      const [removed] = sourceItems.splice(source.index, 1);
      console.log(removed);
      console.log(sourceItems);

      // sourceItem index 
      console.log(sourceItems);
      sourceItems.forEach((element) => {
        if (element.todoIndex > source.index) {
          element.todoIndex -= 1;
        }
      });

      // Column  condition index 
      removed.status = destination.droppableId;
      //  Item  index destination index 
      console.log(removed);

      removed.todoIndex = destination.index;
      // destination index  
      destItems.forEach((element) => {
        if (element.todoIndex >= destination.index) {
          element.todoIndex += 1;
        }
      });

      destItems.splice(destination.index, 0, removed);

      setColumns({
        ...columns,
        [source.droppableId]: {
          ...sourceColumn,
          items: sourceItems,
        },
        [destination.droppableId]: {
          ...destColumn,
          items: destItems,
        },
      });
      sendToDosFunc(columnsFromBackend);
    } else {
      //  Droppable  
      const column = columns[source.droppableId];
      const copiedItems = [...column.items];
      const [removed] = copiedItems.splice(source.index, 1);

      removed.todoIndex = destination.index;

      copiedItems.splice(destination.index, 0, removed);

      /*
     index    index  
    */

      // Droppable Item Index 
      let test = 0;
      copiedItems.forEach((element) => {
        element.todoIndex = test;
        test++;
      });

      console.log(copiedItems);
      setColumns({
        ...columns,
        [source.droppableId]: {
          ...column,
          items: copiedItems,
        },
      });
      sendToDosFunc(columnsFromBackend);
    }
  };
  // Drag And Drop  

  //  todo    
  const onClickAddOpen = (e) => {
    setIsClick((prev) => !prev);
    setToDoAdd({
      content: "",
      date: new Date().toLocaleDateString(),
    });
  };

  //  todo  textfield  
  const toDoInputFunc = (e) => {
    const { name, value } = e.target;
    setToDoAdd({ ...toDoAdd, [name]: value });
  };

  //  todo   
  const setDate = (newDate) => {
    const setNewDate = newDate.toLocaleDateString();
    console.log(setNewDate);
    setToDoAdd({ ...toDoAdd, deadline: setNewDate });
  };

  //  toDo  
  const toDoAddFunc = (e) => {
    e.preventDefault();
    // newItem  

    const newItem = {
      status: "READY",
      todoIndex: columns["READY"].items.length,
      content: toDoAdd.content,
      deadline: toDoAdd.deadline,
      isEditing: false,
      lastUpdateId: userName,
      id: uuid(),
    };

    //  toDo columns 

    const newArr = [...columnsFromBackend, newItem];

    setColumnsFromBackend([...columnsFromBackend, newItem]);

    sendToDosFunc(newArr);
    //      dialog 
    setIsClick((prev) => !prev);
  };

  // toDo 
  const toDoDeleteFunc = async (e) => {
    if (window.confirm(" ?")) {
      const { id, value } = e.target;
      console.log(id, value);
      let sortIndex = 0;

      //  Condition   
      const anotherConditionArr = columnsFromBackend.filter(
        (element) => element.status !== id
      );

      // filter     item    

      const updateArr = columns[id].items.filter((delId) => {
        if (delId.id === value) {
          sortIndex = delId.todoIndex;
        }
        return delId.id !== value;
      });
      updateArr.forEach((element) => {
        if (element.todoIndex > sortIndex) {
          element.todoIndex -= 1;
        }
      });
      const newCols = [...anotherConditionArr, ...updateArr];
      console.log("delete and new cols", newCols);

      await deleteToDos(value);

      await sendToDosFunc(newCols);
      setColumnsFromBackend([...anotherConditionArr, ...updateArr]);
    }
  };

  const deleteToDos = async (id) => {
    console.log(id);
    const res = await axios({
      method: "delete",
      url: `/room/${param}/${id}`,
    });
    if (res.status === 200) {
      console.log("delete");
    }
  };

  //  toDo
  const [columnsFromBackend, setColumnsFromBackend] = useState([]);

  // condition  toDo
  const [columns, setColumns] = useState({
    READY: {
      name: " ",
      items: [],
    },
    PROCESSING: {
      name: " ",
      items: [],
    },
    DONE: {
      name: " ",
      items: [],
    },
    DEFER: {
      name: " ",
      items: [],
    },
  });

  //   click 
  const [isClick, setIsClick] = useState(false);

  //  (condition, todoIndex, content, deadline, isEditing)
  const [toDoAdd, setToDoAdd] = useState({
    status: "READY",
    todoIndex: 0,
    content: "",
    deadline: "",
    isEditing: false,
    id: "",
    lastUpdateId: userName,
  });

  // todo 
  useEffect(() => {
    itemforEach(columnsFromBackend);
  }, [columnsFromBackend]);

  // ToDo  , ,   
  const itemforEach = async (items) => {
    const newToDos = {
      ...columns,
      READY: {
        name: " ",
        items: [],
      },
      PROCESSING: {
        name: " ",
        items: [],
      },
      DONE: {
        name: " ",
        items: [],
      },
      DEFER: {
        name: " ",
        items: [],
      },
    };
    await items.forEach((element) => {
      newToDos[element.status].items.push(element);
    });
    setColumns(newToDos);
  };

  const sendToDosFunc = (sendToDos) => {
    handler(sendToDos);
  };

  /* todo websocket*/
  const client = useRef(null);

  useEffect(() => {
    connect();
    return () => disConnect();
  }, []);

  const subscribe = () => {
    console.log("subscribe");
    if (client.current != null) {
      client.current.subscribe(`/topic/todo/${param}`, (data) => {
        const newMessage = JSON.parse(data.body);
        setColumnsFromBackend(newMessage);
        console.log(newMessage);
        //    
      });
    }
  };

  const connect = () => {
    client.current = new Client({
      brokerURL: "ws://localhost:8080/ws/websocket",
      debug: function (str) {
        console.log(str);
      },
      onConnect: () => {
        subscribe();
      },
    });
    client.current.activate();
  };

  const handler = (sendToDos1) => {
    if (client.current != null) {
      if (!client.current.connected) {
        return;
      }
      console.log("handler", sendToDos1);
      client.current.publish({
        destination: `/app/todo/${param}`,
        body: JSON.stringify({
          todos: sendToDos1,
        }),
      });
    }
  };

  const disConnect = () => {
    if (client.current != null) {
      if (client.current.connected) client.current.deactivate();
    }
    console.log("disconnected");
  };
  /* todo websocket*/

  //    dialog  
  const [open, setOpen] = useState(false);

  //  toDo, ToDoDetail  
  const [writeTodo, setWriteTodo] = useState();

  const onClickFunczz = (item, e) => {
    setWriteTodo(item);
    setOpen(true);
  };

  const closeDialog = () => {
    setOpen(false);
  };

  /* 05.08 ToDo   */
  const writeTodoFunc = (childToDo) => {
    const fixToDo = childToDo;
    fixToDo.lastUpdateId = userName;
    console.log("fixed!!!", fixToDo);

    const newCol = [
      ...columnsFromBackend.filter((element) => element.id !== fixToDo.id),
      fixToDo,
    ];

    setColumnsFromBackend((tt) => [
      ...tt.filter((element) => element.id !== fixToDo.id),
      fixToDo,
    ]);

    console.log("new col", newCol);
    console.log(columnsFromBackend);
    sendToDosFunc(newCol);
  };

  console.log(columnsFromBackend);

  return (
    <>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          padding: "30px 0px 0px 0px",
        }}
      >
        <DragDropContext
          onDragEnd={(result) => onDragEnd(result, columns, setColumns)}
          onDragStart={(result) => onDragStart(result, columns, setColumns)}
        >
          {Object.entries(columns).map(([columnId, column], index) => {
            return (
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  width: "320px",
                }}
                key={columnId}
              >
                <h3>{column.name}</h3>
                <div style={{ margin: 15 }}>
                  <Droppable droppableId={columnId} key={columnId}>
                    {(provided, snapshot) => {
                      return (
                        <div
                          {...provided.droppableProps}
                          ref={provided.innerRef}
                          style={{
                            background: snapshot.isDraggingOver
                              ? "skyblue"
                              : "lightgrey",
                            padding: 4,
                            width: "290px",
                            borderRadius: "25px 25px 25px 25px",
                          }}
                        >
                          {column.items.map((item, index) => {
                            return (
                              <Draggable
                                key={item.id}
                                draggableId={item.id}
                                index={index}
                              >
                                {(provided, snapshot) => {
                                  return (
                                    <div
                                      ref={provided.innerRef}
                                      {...provided.draggableProps}
                                      {...provided.dragHandleProps}
                                      style={{
                                        userSelect: "none",
                                        padding: 16,
                                        margin: "0 0 8px 0",
                                        minHeight: "50px",
                                        backgroundColor: snapshot.isDragging
                                          ? "#3c473a"
                                          : "grey",
                                        color: "black",
                                        ...provided.draggableProps.style,
                                        borderRadius: "25px 25px 25px 25px",
                                      }}
                                    >
                                      <div>
                                        <Button
                                          style={{
                                            fontSize: "12px",
                                            color: "white",
                                            padding: "0px 0px 0px 0px",
                                            minWidth: "1px",
                                          }}
                                          onClick={(e) =>
                                            onClickFunczz(item, e)
                                          }
                                        >
                                          <div
                                            style={{
                                              fontSize: "12px",
                                              whiteSpace: "nowrap",
                                              overflow: "hidden",
                                              textOverflow: "ellipsis",
                                              maxWidth: "290px",
                                              textAlign: "left",
                                            }}
                                          >
                                            {item.content}
                                          </div>
                                        </Button>
                                      </div>
                                      <div
                                        style={{
                                          fontSize: "10px",
                                          color: "black",
                                          height: "20px",
                                        }}
                                      >
                                        <Button
                                          style={{
                                            color: "black",
                                            height: "20px",
                                            padding: "0px 0px 0px 0px",
                                          }}
                                        >
                                          {item.deadline}
                                        </Button>
                                        <div
                                          style={{
                                            display: "inline-block",
                                            align: "right",
                                            height: "20px",
                                          }}
                                        >
                                          <Button
                                            style={{
                                              fontSize: "10px",
                                              height: "20px",
                                              padding: "0px 0px 0px 125px",
                                            }}
                                            color="error"
                                            onClick={toDoDeleteFunc}
                                            value={item.id}
                                            id={item.status}
                                            disableElevation
                                            startIcon={<DeleteForeverIcon />}
                                          >
                                            
                                          </Button>
                                        </div>
                                      </div>
                                    </div>
                                  );
                                }}
                              </Draggable>
                            );
                          })}
                          {provided.placeholder}
                        </div>
                      );
                    }}
                  </Droppable>
                </div>
              </div>
            );
          })}
        </DragDropContext>
      </div>
      <div style={{ textAlign: "center", height: "100%" }}>
        {isClick ? (
          <div
            style={{
              maxWidth: "500px",
              display: "inline-block",
              textAlign: "center",
            }}
          >
            <form type="submit" onSubmit={toDoAddFunc}>
              <TextField
                margin="dense"
                name="content"
                label="ToDo "
                type="text"
                fullWidth
                variant="standard"
                onChange={toDoInputFunc}
                value={toDoAdd.content}
                required
              />
              <LocalizationProvider dateAdapter={AdapterDateFns}>
                <DesktopDatePicker
                  label="Date desktop"
                  inputFormat="MM/dd/yyyy"
                  value={toDoAdd.deadline}
                  onChange={setDate}
                  renderInput={(params) => <TextField {...params} />}
                />
              </LocalizationProvider>
              <Button
                type="submit"
                endIcon={<PublishIcon />}
                variant="outlined"
                color="success"
              >
                
              </Button>
              <Button
                onClick={onClickAddOpen}
                endIcon={<CancelIcon />}
                variant="outlined"
                color="error"
              >
                
              </Button>
            </form>
          </div>
        ) : (
          <>
            <Button
              onClick={onClickAddOpen}
              endIcon={<AddTaskIcon />}
              variant="contained"
            >
               
            </Button>
            <Button onClick={sendToDosFunc}>todotest</Button>
          </>
        )}
      </div>
      <div style={{ textAlign: "right" }}>
        <RoomSetting inviteCode={inviteCode} />
      </div>
      {open ? (
        <ToDoDetail
          func={closeDialog}
          writeTodo={writeTodo}
          writeTodoFunc={writeTodoFunc}
        />
      ) : (
        ""
      )}
      <div style={{ textAlign: "right" }}>
        <Button
          style={{ width: "145px", margin: "5px 0 0 10px" }}
          variant="outlined"
          endIcon={<ChatIcon />}
        >
          <Link
            to={`/room/chat/${param}`}
            style={{
              textDecoration: "none",
              color: "gray",
            }}
          >
            
          </Link>
        </Button>
      </div>
    </>
  );
}

export default RoomDetail;
