const Validation = (values) => {
  let errors = {};

  if (!values.name) {
    errors.fullname = "  .";
  }
  if (!values.id) {
    errors.id = "  .";
  }
  if (!values.email) {
    errors.email = "  .";
  } else if (!/\S+@\S+\.\S+/.test(values.email)) {
    errors.email = "  .";
  }
  if (!values.pw) {
    errors.password = "  .";
  } else if (values.pw.length < 5) {
    errors.password = " 5  .";
  }

  return errors;
};

export default Validation;
